<template>
	<div class="page4_4">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title="报告要素疑点" id="more4_4" ydname="审计报告自动生成"/>
			</div>
		</div>
		<div class="nav">
			<ul class="fl">
				<li>
					<router-link active-class="active" class="item" :to="{path:'/inside/page4_4'}">
						报告纠偏
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="交互调整" id="more4_4"/>
			</div> -->
		</div>
		<div class="content_page">
			<div class="btn-group">
				<el-row style="text-align: center;margin-bottom: 20px;">
				  <el-button :class="{active:btnIndex==1}" @click="updateOption(1)" type="danger" plain>疑点</el-button>
				  <el-button :class="{active:btnIndex==2}" @click="updateOption(2)" type="warning" plain>结论</el-button>
				  <el-button :class="{active:btnIndex==3}" @click="updateOption(3)" type="info" plain>建议</el-button>
				  <el-button :class="{active:btnIndex==4}" @click="updateOption(4)" type="success" plain>重点</el-button>
				</el-row>
			</div>
			<div id="scatterEchart" style="width: 100%;height: 700px;margin: 0 auto"></div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import interBtn from './interactions'
	import Place from './page/place'
	import * as echarts from 'echarts'
	export default{
		name:'page4_4',
		components:{
			interBtn,
			Place,
		},
		data(){
			return{
				msg:["审计报告自动生成","报告纠偏"],
				btnIndex:1
			}
		},
		computed:{
			getWord(){
				return this.$store.getters.getWord;
			}
		},
		methods:{
			optionScatter(data){
				return {
				    title: {
				        text: '比较目标审计单位中审计意见（审计其它要素）与类案审计结论的偏差程度'
				    },
				    legend: {
				        right: 10,
				        data: ['类案审计','目标审计']
				    },
				    xAxis: {
				    	name:data[3]?(data[2]+'（'+data[3]+'）'):data[2],
				    	type:data[3]?'value':'category',
				        splitLine: {
				            lineStyle: {
				                type: 'dashed'
				            }
				        },
				        scale: true
				    },
				    yAxis: {
				    	name:'时间',
				    	type:'category',
				    	splitLine: {
				            lineStyle: {
				                type: 'dashed'
				            }
				        },
				        
				    },
				    tooltip:{
			        	show:true,
			        	formatter: function (param) {
			        		console.log(param);
		                    return param.data[1]+'<br/>'+data[2]+'：'+param.data[0]+data[3];
		                },
			        },
				    series: [{
				        name: '类案审计',
				        data: data[0],
				        type: 'scatter',
				        markLine:data[3]?{
				        	data: [
			                    {type: 'max', name: '最大值'},
			                    {type: 'min', name: '最小值'},
			                    {type: 'average', name: '平均值'}
			                ],
			                label:{
			                	formatter:'{b}:{c}'
			                }
				        }:{
				        	data: [],
			               },
				        symbolSize: 30,
				        itemStyle: {
				            normal: {
				                shadowBlur: 10,
				                shadowColor: 'rgba(25, 100, 150, 0.5)',
				                shadowOffsetY: 5,
				                color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [{
				                    offset: 0,
				                    color: 'rgb(129, 227, 238)'
				                }, {
				                    offset: 1,
				                    color: 'rgb(25, 183, 207)'
				                }])
				            }
				        }
				    },{
				        name: '目标审计',
				        data: data[1],
				        type: 'scatter',
				        // markLine:{
				        // 	data: [
			         //            {x: 0, y: data[1][0][0]},
			         //            {x: data[1][0][1], y: data[1][0][0]},
			         //        ],
			         //        label:{
			         //        	formatter:'{b}:{c}'
			         //        }
				        // },
				        symbolSize: 30,
				        itemStyle: {
				            normal: {
				                shadowBlur: 10,
				                shadowColor: 'rgba(120, 36, 50, 0.5)',
				                shadowOffsetY: 5,
				                color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [{
				                    offset: 0,
				                    color: 'rgb(251, 118, 123)'
				                }, {
				                    offset: 1,
				                    color: 'rgb(204, 46, 72)'
				                }])
				            }
				        }
				    }]
				}
			},
			updateOption(index){
				this.btnIndex=index;
				var myChart = echarts.init(document.getElementById('scatterEchart'));
				let data1=[
						[[121,'2012年12月'],[334,'2013年3月'],[546,'2013年6月'],[342,'2013年9月'],[233,'2013年12月'],[354,'2014年3月'],[223,'2014年6月'],[354,'2014年9月'],[35,'2014年12月'],[32,'2015年3月'],[235,'2015年6月'],[243,'2015年9月'],[765,'2015年12月'],[463,'2016年3月'],[634,'2016年6月'],[346,'2016年9月'],[464,'2016年12月'],[35,'2017年3月'],[565,'2017年6月'],[765,'2017年9月'],[543,'2017年12月'],[534,'2018年3月'],[32,'2018年6月'],[234,'2018年9月']],
						[[535,'2014年12月']],
						'营业总额',
						'万元'
				];
				let data2=[
						[['标准无保留意见','2015年9月'],['保留意见','2015年12月'],['否定意见','2016年3月'],['标准无保留意见','2016年6月'],['无法表示意见','2016年9月'],['无法表示意见','2016年12月'],['保留意见','2017年3月'],['标准无保留意见','2017年6月'],['否定意见','2017年9月'],['无法表示意见','2017年12月'],['保留意见','2018年3月'],['保留意见','2018年6月'],['标准无保留意见','2018年9月']],
						[['标准无保留意见','2017年9月']],
						'结论',''
				];
				let data3=[
						[['建议1','2015年9月'],['建议3','2015年12月'],['建议2','2016年3月'],['建议4','2016年6月'],['建议1','2016年9月'],['建议2','2016年12月'],['建议4','2017年3月'],['建议1','2017年6月'],['建议3','2017年9月'],['建议5','2017年12月'],['建议3','2018年3月'],['建议4','2018年6月'],['建议4','2018年9月']],
						[['建议5','2017年3月']],
						'建议',''
				];
				let data4=[
						[['重点1','2015年9月'],['重点3','2015年12月'],['重点5','2016年3月'],['重点6','2016年6月'],['重点6','2016年9月'],['重点2','2016年12月'],['重点4','2017年3月'],['重点4','2017年6月'],['重点3','2017年9月'],['重点5','2017年12月'],['重点3','2018年3月'],['重点3','2018年6月'],['重点4','2018年9月']],
						[['重点3','2016年3月']],
						'重点',''
				];
				if(index==1){
					myChart.setOption(this.optionScatter(data1));
				}else if(index==2){
					myChart.setOption(this.optionScatter(data2));
				}else if(index==3){
					myChart.setOption(this.optionScatter(data3));
				}else if(index==4){
					myChart.setOption(this.optionScatter(data4));
				}
				$(window).resize(function(){
					myChart.resize({
						width:'auto',
						height:'auto'
					})
				});
			}
		},
		mounted(){
			this.updateOption(1);
		}
	}
</script>
<style type="text/css" scoped="">
	.el-button--danger.is-plain.active{
		background: #dd6161;
	    border-color: #dd6161;
	    color: #fff;
	    outline: 0;
	}
	.el-button--info.is-plain.active {
	background: #82848a;
	border-color: #82848a;
	color: #fff;
	outline: 0;
	}
	.el-button--warning.is-plain.active {
	    background: #cf9236;
	    border-color: #cf9236;
	    color: #fff;
	    outline: 0;
	}
	.el-button--success.is-plain.active {
	    background: #5daf34;
	    border-color: #5daf34;
	    color: #fff;
	    outline: 0;
	}
</style>


